* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  max-height: 40px;
  display: flex;
}

a {
  text-decoration: none;
}

.nav__list {
  list-style-type: none;
  display: flex;
}

.nav__item {
  text-align: center;
  text-transform: uppercase;
  margin-right: 20px;
  display: inline-block;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  text-align: center;
  color: #060b35;
  height: 60px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: " ";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 3px;
  display: block;
}

.card__title {
  color: #060b35;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  padding-top: 4px;
  padding-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  display: flex;
}

.card__rate--description {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  padding-top: 24px;
  padding-bottom: 16px;
  display: flex;
}

.card__price-label {
  color: #616070;
  padding-top: 0;
  padding-bottom: 0;
  font-size: 12px;
}

.card__price-value {
  color: #060b35;
  padding-top: 0;
  padding-bottom: 0;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.main {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  grid-template-rows: repeat(2, 408px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  line-height: 18px;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:first-child, .stars__star:nth-child(2), .stars__star:nth-child(3), .stars__star:nth-child(4), .stars__star:nth-child(5) {
  background-image: url("star-active.edda4c43.svg");
}
